.learn-more-link-container {
  overflow: hidden;
  height: 31px;
  max-width: max-content;
}

.learn-more-link {
  width: max-content;
  position: relative;
  padding-bottom: 11px;
  color: $green-text-color;
  font-size: 14px;
  font-weight: 600;
  letter-spacing: 0.05em;
  transition: color 0.3s linear;

  &:hover {
    color: $non-active-grey-color;
  }
}

.learn-more-link::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 1px;
  background-color: $green-text-color;
  transition: $transform-duration;
}

.learn-more-link:hover::after {
  transform: translateX(101%);
}
